:root {
    /* Colours */
    --blue: #3eb0ef;
    --green: #a4d037;
    --purple: #ad26b4;
    --yellow: #fecd35;
    --red: #f05230;
    --darkgrey: #15171A;
    --midgrey: #738a94;
    --lightgrey: #c5d2d9;
    --whitegrey: #e5eff5;
    --pink: #fa3a57;
    --brown: #a3821a;
}

/* D2Coding */

@font-face {
    font-family: "D2Coding";
    font-style: normal;
    font-weight: normal;
    src: url( "./font/subset-D2Coding.woff2" ) format( "woff2" ),
                    url('./font/subset-D2Coding.woff') format('woff');
    font-display: swap;
}

@font-face {
    src: url("./font/Maplestory Light.woff2") format( "woff2" );
    font-family: "maplestory"
}

body {
    line-height: 1;
    -webkit-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}

pre > code { 
    font-size: 14px;
    font-family: "D2coding" !important;
}

pre.highlight ::selection {
    background: green;
    color: white;
}

pre.highlight {
    -webkit-user-select:text;
    -moz-user-select:text;
    -ms-user-select:text;
    user-select:text;
}

/* 타이틀 효과 */
.post-full-title, .site-title {
    font-weight: bold;
    word-break: break-all;
    line-height: initial;
    will-change: transform;
    transform: translateZ(0);
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    letter-spacing: .02em;
    text-shadow: 10px 10px 8px  rgb(0 0 0 / 15%);
    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 250% 500%;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    animation: textAnim 5s ease-out infinite, nudgeText 5s ease-out infinite;
    transform: translate3d(0,0,0);
    backface-visibility: hidden;
}

/* 타이틀 수정 */

.post-full-content h1 {
    color: color(#3eb0ef lightness(-20%));
    counter-reset: numbering;
}

.post-full-content h1:first-of-type {
    margin-top: 0;
}

.post-full-content h1::before {
    content: "\f02c";
}

.post-full-content h2 {
    color: #3eb0ef;
    counter-reset: numbering;
}

.post-full-content h1::before,
.post-full-content h2::before {
    margin: 0 10px 0 4px;
    font-family: "FontAwesome";
    padding: 0px 5px 6px 0px;
}

.post-full-content h2:before {
    content: '\f0d7';
}

.post-full-content h3 {
    color: teal;
}

.post-full-content h2 + h3, .post-full-content h2 + p {
    margin-top: 1.5em;
}

.post-full-content h3::before {
    counter-increment: numbering;
    content: counter(numbering) ".";
    margin-right: 10px;
}

.post-full-content h3 + p,
.post-full-content h6 + p {
    margin-top: 1.5em;
}

.main-title {
    -webkit-text-stroke: snow 1px;
}

.post-full-meta {
    margin-bottom: 10px;
}

@keyframes textAnim {
	0% {
		background-position: 0 50%
	}
	50% {
		background-position: 100% 50%
	}
	100% {
		background-position: 0 50%
	}
}

@keyframes nudgeText {
	0% {
		transform: translateY(0)
	}
	20% {
		transform: translateY(2px)
	}
	30% {
		transform: translateY(0)
	}
	50% {
		transform: translateY(2px)
	}
	60% {
		transform: translateY(0)
	}
	80% {
		transform: translateY(2px)
	}
	90% {
		transform: translateY(0)
	}
}

/* Search 결과화면을 위한 CSS 설정 시작 */
ul.mylist li, ol.mylist li {
    padding: 5px 0px 5px 5px;
    margin-bottom: 5px;
    border-bottom: 1px solid #efefef;
    font-size: 12px;
}

ul.mylist li:last-child,
ol.mylist li:last-child {
    border-bottom: 0px;
}

ul.mylist li,
ol.mylist li {
    -webkit-transition: background-color 0.3s linear;
    -moz-transition: background-color 0.3s linear;
    -ms-transition: background-color 0.3s linear;
    -o-transition: background-color 0.3s linear;
    transition: background-color 0.3s linear;
}

ul.mylist li:hover,
ol.mylist li:hover {
    background-color: #f6f0b6;
}
/* Search 결과화면을 위한 CSS 설정 끝 */

/* 링크뒤쪽에 FontAwesome표현을 위한 custom CSS */
