    max-width: 250px;
    line-height: 1rem;
    width: 100%;
    font-size: 2.5rem;
    margin-bottom: 0.5em;
    font-weight: bold;
    color: #ce180b;
    font-family: 'maplestory', Georgia, serif !important;
    align-self: flex-start;
}

div.table-of-contents-list::before {
    content: '\f05a';
    display: inline-block;
    margin: 0 2px 0 4px;
    line-height: 1;
    font-family: "FontAwesome";
    font-size: 2rem;
    font-weight: normal;
    font-style: normal;
    vertical-align: middle;
    padding: 5px 5px 6px 0px;
}

ul.table-of-contents-list, ol.table-of-contents-list {
    list-style: none;
    margin: 0 0 1.5rem;
    padding: 0px;
    max-width: 250px;
