/* toc */

#toc {
    position: absolute;
    right: calc((100vw - 1058px) / -2);
    top: 70px;
    color: black;
    background-color: white;
    overflow-y: auto;
    overflow-x: hidden;
    padding: 0 1.75rem 0 0;
    margin-right: 0;
    font-size: 0.6em;
    display: none;
    font-family: 'maplestory';
    width: calc((100vw - 1058px) / 2);
}

#toc.floating-active {
    position: fixed; 
    transition: none;
    padding-top: 90px;
    right: 0;
    top: 0;
}

#toc.floating-active.toc-hidden {
    display: none;
}

#toc ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

#toc > ul {
    border-left: 2px solid #e0d9e7;
}

#toc ul li {
    padding: 0;
    margin: 0 0 0.5rem;
}

#toc > ul > li {
    margin-left: -2px;
}

#toc a {
    color: black;
    text-decoration: none;
    box-shadow: none;
    display: flex;
    margin-bottom: 0.5rem;
    padding-left: 10px;
}

#toc a span {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
   
#toc a:hover {
    color: var(--pink);
}

#toc a.toc-active {
    font-weight: bold; 
    transition: all 0.125s ease-in 0s; 
    font-size: 1.5rem;
    color: var(--pink);
    border-left: 3px solid var(--pink);
    animation: blinker-toc-title 2s linear infinite;
    font-weight: bold;
    padding-left: 8px;
}

#toc ul li a.toc-active::before {
    color: color(var(--pink) whiteness(25%));
}

#toc a.toc-active::after {
    content: "\f060";
    margin-left: 5px;
    font-family: "FontAwesome";
}

#toc ul > li a::before {
    content: "\f02c";
    font-family: "FontAwesome";
    padding-right: 5px;
    font-size: 0.8em;
    color: #737373;
    opacity: 0.7;
}

#toc > ul > li > ul > li > a::before {
    content: "\f0d7";
    padding-left: 10px;
}

#toc > ul > li > ul > li > a.toc-active::before {
    padding-left: 8px;
}

#toc > ul > li > ul > li > ul {
    counter-reset: tocTitle3;
}

#toc > ul > li > ul > li > ul > li > a::before {
    counter-increment: tocTitle3;
    content: counter(tocTitle3) ".";
    padding-left: 20px;
    flex-shrink: 0;
    color: inherit;
    font-family: inherit;
}

#toc > ul > li > ul > li > ul > li > a.toc-active::before {
    padding-left: 18px;
}

h6.sol::before {
    content: "[v]";
    width: 30px;
    display: inline-block;
}

h6.opacity-0::before {
    content: "[ ]";
}

.opacity-0 + blockquote > p {
    opacity: 0;
}

@keyframes blinker-toc-title {
	0% {
		opacity: 0.5;
	}
  50% {
		opacity: 1.0;
  }
	100% {
		opacity: 0.5;
	}
}

@media (min-width: 1300px){
    #toc {
        display: block;
    }
}

@media (max-width: 1600px){
    .post-full-content {
        padding: 70px 70px 0;
